export const BIGRAM_FREQUENCIES = {};

for (const wordLength in ALL_ACTUAL) {
  const actualList = ALL_ACTUAL[wordLength];
  const biFreq = {};
  let totalBigrams = 0;
  for (const i_s in ALL_LETTERS) {
    for (const i_t in ALL_LETTERS) {
      const bigram = ALL_LETTERS[i_s] + ALL_LETTERS[i_t];
      // console.log("bigram", bigram);
      biFreq[bigram] = 0;
    }
  }
  actualList.forEach((word) => {
    for (let i = 0; i < wordLength - 1; ++i) {
      const bigram = word.slice(i, i + 2);
      ++biFreq[bigram];
    }
  })
  // console.log(biFreq);

  BIGRAM_FREQUENCIES[wordLength] = biFreq;
}