export function RulesModal(props) {
  function setIsShowRulesFalse() {
    props.setIsShowRules(false);
  }

  return (
    <div className={"modal rules " + (props.isShowRules ? "show" : "hide")}>
      <div className="shade" onClick={setIsShowRulesFalse} />
      <div className="main-column">
        <div className="modal-header">
          <div className="modal-close-button" />
          <div className="modal-title">How to Play</div>
          <button className="modal-close-button" value="close-rules" onClick={setIsShowRulesFalse}>&times;</button>
        </div>
        <div className="modal-content">
          <p>Guess the mystery word, just like your other favorite word guessing games. However, you need to only guess words with <strong>uncommon bigrams</strong>. With each guess, your score increases by the frequency of the least common bigram in your guessed word (plus one). Bigram frequencies are taken from the list of candidate answer words.</p>
          <p>Your goal is to <strong>minimize</strong> your score.</p>
          <div className="settings-row">
            <button className="big-button" onClick={setIsShowRulesFalse}>Play Now!</button>
          </div>
        </div>
      </div>
    </div>
  )
}